import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/static/master/data";

class StaticMasterDataService extends BaseApiService {
  getAll = (start = 0, limit = 4, search = "") => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  save(obj: any) {
    const url = `${DEFAULT_PATH}`;
    return this.makePostRequestWithAuth(url, obj);
  }
  approve = (obj: any) => {
    const url = "/custom/api/approval/request";
    return this.makePostRequestWithAuth(url, obj);
  };
  update(uid: string, obj: any) {
    const url = `${DEFAULT_PATH}/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  }
  delete(uid: string) {
    const url = `${DEFAULT_PATH}/${uid}`;
    return this.makeDeleteRequestWithAuth(url);
  }
  findById(id: string) {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  }
  activate = (uid: string) => {
    const url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  Inactivate = (uid: string) => {
    const url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };

  modelToAPIParam = (key: any) => {
    return "";
  };
  getDropdownList = (type: string, active = true, search = "") => {
    const url = `${DEFAULT_PATH}/search?staticMasterType.code=${type}&isActive=${active}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllCode = () => {
    const url = `${DEFAULT_PATH}/all`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new StaticMasterDataService();
